<template>
	<view class="modifyPwd">
		<view class="pwdForm">
			<u-form :model="form" ref="uForm" :label-width="150">
				<view class="pwd_item">
					<u-form-item label="原密码" label-position="left">
						<u-input v-model="form.oldPassword" type="password" placeholder="请输入新密码" />
					</u-form-item>
				</view>
				<view class="pwd_border">
					<u-form-item label="新密码" label-position="left">
						<u-input v-model="form.newPassword" type="password" placeholder="请输入新密码" minLength="6" maxLength="16" />
					</u-form-item>
					<u-form-item label="确认密码" label-position="left">
						<u-input v-model="rePassword" type="password" placeholder="请再次输入新密码" minLength="6" maxLength="16" />
					</u-form-item>
				</view>
			</u-form>
		</view>
		<view class="modifyBtn" @click="submit">确定</view>
	</view>
</template>

<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				form:{
					oldPassword:'',
					newPassword:'',
				},
				rePassword:'',
			}
		},
		methods: {
			// 确定
			submit() {
				if(!this.form.oldPassword){
					Tools.showErrorToast('请输入原密码')
					return
				}
				if(!this.form.newPassword){
					Tools.showErrorToast('请输入新密码')
					return
				}
				if(this.form.newPassword != this.rePassword){
					Tools.showErrorToast('两次密码输入不一致')
					return
				}
				Tools.showLoading('修改中')
				httpUtils.sendHttpRequest('1003', Object.assign({}, this.form), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						Tools.showSuccessToast('修改成功')
						setTimeout(function(){
							uni.reLaunch({
								url: '/pages/login',
							})
						},1000)
					}
				})
			},
		}
	}
</script>

<style lang="scss">
	page{
		background-color: #f6f6f6;
	}
	.modifyPwd {
		.pwdForm{
			background-color: #fff;
			.pwd_item{
				padding: 0 30rpx;
			}
			.pwd_border{
				border-top: 20rpx solid #f6f6f6;
				padding: 0 30rpx;
			}
		}
		.modifyBtn {
			width: 520rpx;
			height: 90rpx;
			line-height: 90rpx;
			border-radius: 44rpx;
			text-align: center;
			background: linear-gradient(90deg, #03958C, #00AE66);
			font-size: 32rpx;
			color: #fff;
			margin: 100rpx auto 0;
			// display: flex;
			// justify-content: center;
			// margin-top: 20rpx;
			// u-button{
			// 	padding: 30rpx;
